.nav {
  &__item {
    display: inline-block;
    margin-left: 20px;
    margin-right: 20px; }
  &__link {
    font-size: 15px;
    transition: color .3s;
    @include hover {
      color: #828282; } }

  &--brands {
    &__link {
      text-transform: uppercase;
      @include light;
      font-size: 15px;
      color: #fff; } }
  &--footer {
    &__item {
      display: block;
      margin-left: 0;
      margin-right: 0;
      &:not(:last-child) {
        margin-bottom: 22px; } } } }
