.section__block p { font-size: 15px; margin-bottom: 35px; font-family: 'helveticaneuecyr-light', Arial, sans-serif; }

.section__block p strong { font-family: 'helveticaneuecyr-bold', Arial, sans-serif; }

.section__block p a { color: #025db9; text-decoration: underline; }

.section__block p.has-no-mb { margin-bottom: 0; }

.section--bg { background-repeat: no-repeat; background-size: cover; background-position: 50% 50%; }

.section--bg .container { position: relative; z-index: 1; }

.section--pt-sm { padding-top: 1%; }

.car-bg { background-repeat: no-repeat; background-size: cover; background-position: 50% 50%; background-size: 100%; height: 100%; position: absolute; top: 0; right: 0; width: 36.5%; z-index: 0; }

.models-info__icon { position: absolute; top: 0; left: 0; }

.models-info__icon .icon { font-size: 30px; fill: #414141; }

.models-info__icon .icon-speedometer { font-size: 45px; transform: translate(-13px, -7px); }

.models-info__item { text-align: left; height: 100%; position: relative; }

@media only screen and (max-width: 991px) { .models-info__item { margin-bottom: 10px; } }

.models-info__item::before { content: ''; position: absolute; height: 48px; top: 5px; right: 0; width: 1px; background-color: #414141; }

@media only screen and (max-width: 991px) { .models-info__item::before { display: none; } }

.models-info__item.has-no-line::before { display: none; }

.models-info__item.has-icon { padding-left: 40px; }

.models-info__title { font-family: 'helveticaneuecyr-bold', Arial, sans-serif; font-size: 32px; line-height: 1.15; }

.models-info__title span { font-size: 15px; }

.models-info__subttl { font-size: 15px; color: #9b9b9b; }

.models-info__link { position: absolute; top: -15px; transform: translate(0, -100%); }

@media only screen and (max-width: 991px) { .models-info__link { position: static; transform: translate(0, 0); margin-bottom: 5px; } }

.new-item { box-shadow: 0px 0px 12.92px 4.08px rgba(0, 0, 1, 0.15); background-color: #fff; }

.new-item__title { position: relative; padding-bottom: 10px; margin-bottom: 10px; }

.new-item__title::before { content: ''; position: absolute; bottom: 0; left: 0; width: 53px; height: 1px; background-color: #414141; }

.new-item__img { background-repeat: no-repeat; background-size: cover; background-position: 50% 50%; background-size: contain; background-color: #e8e8e8; padding-top: 45px; padding-bottom: 45px; }

.new-item__img img { max-width: 100%; margin-left: auto; margin-right: auto; }

.new-item__list { margin-bottom: 10px; }

.new-item__top { padding-left: 30px; padding-right: 30px; padding-top: 15px; }

.new-item__btns { display: flex; height: 64px; }

.new-item__btns .btn { width: 50%; position: relative; }

.new-item__btns .btn:first-child::before { content: ''; width: 1px; height: 100%; position: absolute; background-color: #e1e1e1; top: 0; right: 0; }

.item-list__title { font-weight: 400; font-family: 'helveticaneuecyr-bold', Arial, sans-serif; font-size: 15px; margin-right: 10px; text-align: left; width: 28.88889%; }

.item-list__text { font-family: 'helveticaneuecyr-light', Arial, sans-serif; width: 71.11111%; }

.item-list__text.is-green { color: #10ad44; }

.consultation { position: relative; }

.consultation__right { width: 50%; position: absolute; z-index: 2; top: 0; right: 0; height: 100%; width: 36.5%; background-color: #000; }

.consultation__right .slick-slide > div { height: 100%; }

.consultation__right .consultation-slider, .consultation__right .slider--consultation__wrap, .consultation__right .slider, .consultation__right .slick-list, .consultation__right .slick-track, .consultation__right .slide { height: 100%; }

.consultation__top { background-color: #f7f7f7; padding-top: 75px; padding-bottom: 75px; border-bottom: 1px solid  #e6e6e6; }

.consultation__bottom { padding-top: 75px; padding-bottom: 85px; }

.consultation__title { margin-bottom: 20px; }

.form--consultation .form__row { display: flex; margin-bottom: 40px; }

.form--consultation .form__field:not(:last-child) { margin-right: 60px; }

.input input { height: 32px; border-bottom: 1px solid  #202020; padding-top: 5px; padding-bottom: 5px; min-width: 282px; }

.input input::placeholder { color: #979797; font-size: 15px; font-family: 'helveticaneuecyr-light', Arial, sans-serif; }

.two-banners { display: flex; }

.banner-half { width: 50%; background-repeat: no-repeat; background-size: cover; background-position: 50% 50%; padding-top: 32%; position: relative; }

.banner-half:hover .banner-half__btn { max-height: 50px; overflow: visible; }

.banner-half__block { position: absolute; z-index: 1; bottom: 10px; left: 7.8125%; bottom: 22.74194%; max-width: 385px; }

.banner-half__title { margin-bottom: 15px; }

.banner-half__title .title { margin-bottom: 10px; }

.banner-half__btn { max-height: 0; overflow: hidden; transition: max-height .3s; }

.banner-half__bg { position: absolute; width: 100%; height: 100%; z-index: 0; top: 0; left: 0; overflow: hidden; }

.banner-half__bg video { width: 100%; height: 100%; object-fit: cover; }

.links-list__item:not(:last-child) { margin-bottom: 4px; }

.links-list__link { text-transform: uppercase; font-size: 15px; font-family: 'helveticaneuecyr-bold', Arial, sans-serif; transition: color .3s; }

.no-touch .links-list__link:hover { color: #10ad44; }

.brand-nav { background-color: #191919; padding-top: 10px; padding-bottom: 10px; }

.brand-nav__inner { display: flex; padding-top: 10px; padding-bottom: 10px; align-items: center; }

.brand-nav__title { font-size: 15px; color: #fff; padding-right: 20px; position: relative; border-right: 1px solid  #464646; padding-top: 10px; padding-bottom: 10px; min-width: 120px; }

.footer { padding-top: 90px; padding-bottom: 90px; }

.footer__title { padding-bottom: 20px; position: relative; margin-bottom: 25px; }

.footer__title::before { content: ''; position: absolute; width: 53px; height: 1px; background-color: #bcbcbc; bottom: 0; left: 0; }

.footer-contacts__phone { color: #191919; font-size: 15px; font-family: 'helveticaneuecyr-bold', Arial, sans-serif; margin-bottom: 22px; }

.footer-contacts__text { font-size: 15px; color: #727171; margin-bottom: 22px; font-family: 'helveticaneuecyr-light', Arial, sans-serif; }

.footer-contacts__link { font-size: 15px; text-decoration: underline; color: #10ad44; margin-bottom: 22px; display: inline-block; font-family: 'helveticaneuecyr-light', Arial, sans-serif; }

.footer-contacts__copy { text-transform: uppercase; font-family: 'helveticaneuecyr-light', Arial, sans-serif; font-size: 12px; }

.footer-contacts__social { margin-bottom: 22px; }

.social__item { display: inline-block; vertical-align: top; }

.social__item:not(:last-child) { margin-right: 5px; }

.social__link { display: flex; background-color: #10ad44; align-items: center; justify-content: center; width: 34px; height: 34px; }

.social__link .icon { fill: #fff; font-size: 18px; }

.social--sm .social__link { width: 28px; height: 28px; }
