.form {
  &--consultation {
    .form {
      &__row {
        display: flex;
        margin-bottom: 40px; }
      &__field {
        &:not(:last-child) {
          margin-right: 60px; } } } } }
.input {
  input {
    height: 32px;
    border-bottom: 1px solid  #202020;
    padding-top: 5px;
    padding-bottom: 5px;
    min-width: 282px;
    @include placeholder {
      color: #979797;
      font-size: 15px;
      @include light; } } }

