.models-info {
  &__icon {
    position: absolute;
    top: 0;
    left: 0;
    .icon {
      font-size: 30px;
      fill: $c-text; }
    .icon-speedometer {
      font-size: 45px;
      transform: translate(-13px, -7px); } }
  &__item {
    text-align: left;
    height: 100%;
    position: relative;
    @include md {
      margin-bottom: 10px; }
    &::before {
      content: '';
      position: absolute;
      height: 48px;
      top: 5px;
      right: 0;
      width: 1px;
      background-color: $c-text;
      @include md {
        display: none; } }
    &.has-no-line {
      &::before {
        display: none; } }
    &.has-icon {
      padding-left: 40px; } }
  &__title {
    @include bold;
    font-size: 32px;
    line-height: 1.15;
    span {
      font-size: 15px; } }
  &__subttl {
    font-size: 15px;
    color: #9b9b9b; }
  &__link {
    position: absolute;
    top: -15px;
    transform: translate(0, -100%);
    @include md {
      position: static;
      transform: translate(0, 0);
      margin-bottom: 5px; } } }
