@include font('helveticaneuecyr-bold', 'helveticaneuecyr-bold-webfont');
@include font('helveticaneuecyr-roman', 'helveticaneuecyr-roman-webfont');
@include font('helveticaneuecyr-light', 'helveticaneuecyr-light-webfont');
@include font('helveticaneuecyr-medium', 'helveticaneuecyr-medium-webfont');

@mixin roman {
  font-family: 'helveticaneuecyr-roman', Arial, sans-serif; }
@mixin bold {
  font-family: 'helveticaneuecyr-bold', Arial, sans-serif; }
@mixin medium {
  font-family: 'helveticaneuecyr-medium', Arial, sans-serif; }
@mixin light {
  font-family: 'helveticaneuecyr-light', Arial, sans-serif; }

@mixin xl {
  @include r(1440) {
    @content; } }
@mixin lg {
  @include r(1199) {
    @content; } }
@mixin md {
  @include r(991) {
    @content; } }
@mixin sm {
  @include r(767) {
    @content; } }
@mixin xs {
  @include r(575) {
    @content; } }


body {
  background: #FFF;
  line-height: 1.4;
  font-size: 15px;
  @include roman;
  color: $c-text;
  @include no-scroll; }

.out {
  padding-top: 90px; }

.btn {
  color: #fff;
  text-transform: uppercase;
  background-color: transparent;
  // display: inline-flex
  // justify-content: space-between
  // align-items: center
  display: inline-block;
  text-align: center;
  @include light;
  text-align: center;
  font-size: 0;
  height: 50px;

  &__title {
    font-size: 14px;
    // display: inline-flex
    // align-items: center
    // justify-content: center
    display: inline-block;
    vertical-align: top;
    height: 100%;
    background-color: $c-accent;
    padding: 17px 10px;
    text-align: center;
    min-width: 180px;
    transition: color .3s, background-color .3s;
    .icon {
      display: inline-block;
      vertical-align: sub;
      font-size: 16px;
      margin-left: 5px;
      transition: fill .3s; } }
  &__icon {
    display: inline-block;
    background-color: #2c2c2c;
    height: 100%;
    display: inline-flex;
    justify-content: center;
    align-items: center;
    background-color: #2c2c2c;
    width: 50px;
    .icon {
      font-size: 16px;
      fill: #fff; }
    &.is-arrow {
      position: relative;
      &::before {
        content: '';
        display: inline-block;
        @include icon-arrow(12, 12, 2, #fff, 225);
        position: absolute;
        top: 50%;
        transform: translate(-3px, -50%) rotate(225deg); } } }

  &--lg {
    .btn__title {
      min-width: 220px; } }
  &--full {
    height: 100%;
    width: 100%;
    max-width: none;
    .btn__title {
      width: 100%;
      padding: 23px 10px; } }
  &--full-height {
    height: 100%; }
  &--white {
    .btn__title {
      background-color: #fff;
      color: $c-text; }
    @include hover {
      .btn__title {
        background-color: $c-accent;
        color: #fff;
        .icon {
          fill: #fff; } } } }
  &--ttn {
    text-transform: none;
    .btn__title {
      font-size: 15px; } } }

.link {
  font-size: 15px;
  color: #fff;
  display: inline-block;
  padding-right: 100px;
  position: relative;
  transition: color .3s;
  &__title {
    text-decoration: underline;
    transition: color .3s; }
  &__arrow {
    position: absolute;
    right: 0;
    top: 50%;
    transform: translate(0, -50%);
    display: inline-block;
    &::before {
      width: 85px;
      content: '';
      position: absolute;
      height: 1px;
      background-color: $c-text;
      top: 50%;
      right: -1px;
      transform: translate(0, -50%); }
    &::after {
      display: inline-block;
      position: absolute;
      @include icon-arrow(8, 8, 1, $c-text, 225);
      content: '';
      top: 50%;
      transform: translate(0, -50%) rotate(225deg);
      right: 0; } }
  &--green {
    color: $c-accent;
    &::before {
      background-color: $c-text; }
    &::after {
      @include icon-arrow(8, 8, 1, $c-text, 225); } }
  &--sm {
    padding-right: 45px;
    .link__arrow {
      &::before {
        width: 30px; } } }
  &--simple {
    color: $c-text;
    @include hover {
      .link__title {
        color: $c-accent; } }
    .link__title {
      text-decoration: none; }
    .link__arrow {
      &::before {
        background-color: $c-text; }
      &::after {
        border-color: $c-text; } } } }

.phone {
  display: inline-flex;
  align-items: center;
  &__phone {
    @include bold;
    font-size: 32px;
    color: #202020;
    line-height: 1.15; }
  &__icon {
    width: 44px;
    height: 38px;
    background-color: $c-accent;
    margin-right: 10px;
    display: flex;
    align-items: center;
    justify-content: center;
    .icon {
      fill: #fff;
      font-size: 16px;
      stroke: #fff; } } }

.map-link {
  display: flex;
  align-items: center;
  &__icon {
    display: block;
    font-size: 0;
    margin-right: 10px;
    margin-top: 2px;
    .icon {
      font-size: 18px;
      fill: #b6b6b6; } }
  &__title {
    color: #b6b6b6;
    font-size: 14px;
    display: block;
    @include light; } }

.side-btns {
  position: fixed;
  top: 50%;
  transform: translate(0, -50%);
  right: 0;
  z-index: 10; }

.side-btn {
  display: flex;
  align-items: center;
  justify-content: center;
  background-color: $c-accent;
  width: 50px;
  height: 45px;
  position: relative;
  overflow: hidden;
  @include hover {
    overflow: visible;
    .side-btn__title {
      transform: translate(-100%, 0); } }
  .icon {
    fill: #fff;
    font-size: 23px; }
  &:not(:last-child) {
    margin-bottom: 10px; }
  &__title {
    display: flex;
    align-items: center;
    justify-content: center;
    height: 100%;
    position: absolute;
    top: 0;
    left: 0;
    transform: translate(100%, 0);
    background-color: #2c2c2c;
    color: #fff;
    @include light;
    font-size: 14px;
    text-transform: uppercase;
    white-space: nowrap;
    min-width: 180px;
    transition: transform .3s;
    z-index: -1; } }

